$hovercolor: #8A2BE2;
$standardColor: #007bff;
$backgroundColor: #2c3e50;
$textHoverColor: #000000;
$standardMargin: 30px;
$standardFontSize: 12px;
$standardFontFamily: sans-serif;
$standardBorderWidth: 5px;

body {
    font-size: $standardFontSize;
    font-family: $standardFontFamily;
    background: $backgroundColor; }

.btn:hover {
    background-color: $hovercolor;
    color: $textHoverColor; }

.upbtn {
	position: fixed;
	display: none;
	right: 70px;
	bottom: 50px;
	transition: 0.5s all; }

@media ( max-width:  411px) {
    .upbtn {
	    right: 15px;
	    bottom: 10px; } }


.card {
    border-color: $standardColor;
    border-width: $standardBorderWidth; }

.card-body {
    background-color: $backgroundColor;
    color: white; }

.cardInRow {
	margin: $standardMargin; }

nav {
    margin: 27px auto 0;

    position: relative;
    width: 590px;
    height: 50px;
    background-color: $standardColor;
    border-radius: 8px;
    font-size: 0; }

nav a {
    line-height: 50px;
    height: 100%;
    font-size: 15px;
    display: inline-block;
    position: relative;
    z-index: 1;
    text-decoration: none;
    text-transform: uppercase;
    text-align: center;
    color: white;
    cursor: pointer; }

nav .animation {
    position: absolute;
    height: 100%;
    top: 0;
    z-index: 0;
    transition: all .5s ease 0s;
    border-radius: 8px; }

a:nth-child(1) {
    width: 100px; }

a:nth-child(2) {
    width: 110px; }

a:nth-child(3) {
    width: 100px; }

a:nth-child(4) {
    width: 160px; }

a:nth-child(5) {
    width: 120px; }

nav .start-home, a:nth-child(1):hover~.animation {
    width: 100px;
    left: 0;
    background-color: $hovercolor; }

nav .start-about, a:nth-child(2):hover~.animation {
    width: 110px;
    left: 100px;
    background-color: $hovercolor; }

nav .start-blog, a:nth-child(3):hover~.animation {
    width: 100px;
    left: 210px;
    background-color: $hovercolor; }

nav .start-portefolio, a:nth-child(4):hover~.animation {
    width: 160px;
    left: 310px;
    background-color: $hovercolor; }

nav .start-contact, a:nth-child(5):hover~.animation {
    width: 120px;
    left: 470px;
    background-color: $hovercolor; }

nav a:hover {
    color: $textHoverColor;
    text-decoration: none; }

@media ( max-width:  411px) {
    .mainNav {
        width: 100%; } }

.popupButton {
    display: block;
    margin: $standardMargin auto; }

.contactsElement {
    float: left;
    width: 50px;
    margin-left: 50px; }

.contactsContent {
    float: left;
    width: 200px;
    font-size: 17px;
    font-family: $standardFontFamily;
    color: white;
    margin-left: 5px; }

.contactsContainer {
    overflow: hidden;
    margin: $standardMargin; }
